<template>
  <div id="app">
    <div id="v-content" v-bind:style="{minHeight: Height+'px'}">
      <router-view />
    </div>
    <h6 style="margin-bottom: 10px;">&copy;2019  </h6>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      Height: 0
    };
  },
  mounted() {
    // footer   header+footer100
    this.Height = document.documentElement.clientHeight - 50; //
    window.onresize = () => {
      this.Height = document.documentElement.clientHeight - 50;
    };
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



